<template>
  <div class="top-bar d-flex justify-content-center" >


    <div :style="screenWidth > 1200 ? 'min-width: 1200px;': 'width:100%'" class="d-flex justify-content-space-between">
      <div class="d-flex" >
        <div class="btn d-flex align-items-center pl-1 pr-1 cursor" style="height: 39px;" >
          Home
        </div>

        <div v-if="isMobile === false" class="d-flex align-items-center fs-22 fw-500 media-mobile" style="margin-left: 80px;">
          Gam Sa Ham
        </div>
      </div>

      <div class=" d-flex align-items-center">
        <div class="left-button d-flex align-items-center pl-1 pr-1 cursor mr-5" style="height: 100%" @click="clickScrollTo('work')">
          Work
        </div>

        <div class="left-button d-flex align-items-center pl-1 pr-1 cursor mr-5" style="height: 100%" @click="clickScrollTo('about')">
          About
        </div>

        <div class="left-button d-flex align-items-center pl-1 pr-1 cursor mr-3" style="height: 100%; " @click="clickScrollTo('contact')">
          Contact
        </div>


      </div>
    </div>

  </div>
</template>

<script>
export default {
  name: 'TopMenu',
  computed: {
    isMobile() {
      return this.$store.state.isMobile
    },
    screenWidth() {
      return this.$store.state.screenWidth
    }
  },
  methods: {
    clickScrollTo(id){
      let div = document.getElementById(id)

      let top =window.pageYOffset +  div.getBoundingClientRect().top -80

      window.scrollTo(0,top)
    }
  }
}
</script>

