import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)



const store = new Vuex.Store({
    state: {
        isMobile: false,
        screenWidth: 1200
    },
    mutations: {
        SET_IS_MOBILE(state, value) {
            state.isMobile = value
        },
        SET_SCREEN_WIDTH(state, value) {
            state.screenWidth = value;
        }
    },
})

export default store
