<template>
  <div>
    <layout></layout>
  </div>

</template>

<script>


import Layout from './views/layout'

const MAX_SCREEN_WIDTH = 800

export default {
  name: 'App',
  components: {Layout},
  computed: {
    isMobile() {
      return this.$store.state.isMobile
    },
  },
  mounted() {
    if(window) {
      window.addEventListener('resize', this.checkDeviceType)
      this.checkDeviceType();
    }

  },
  destroyed() {
    window.removeEventListener('resize', this.checkDeviceType)
  },
  methods: {
    checkDeviceType() {

      let isMobile = false


        const width = document.body.clientWidth || window.innerWidth

      let screenWidth = 0;

      try {
        screenWidth = Number(width)
      } catch (e) {
        console.error(e)
      }

      if (MAX_SCREEN_WIDTH > screenWidth) {
          isMobile = true
        } else {
          isMobile = false
        }


      this.$store.commit('SET_SCREEN_WIDTH', screenWidth)
      this.$store.commit('SET_IS_MOBILE', isMobile)

    }
  }

}
</script>

